import React from 'react';
import { HashRouter as Router, Route, Redirect } from 'react-router-dom';
import { Provider } from 'react-redux';
import ReactAplayer from 'react-aplayer';

import { configureStore } from '../store';

import SideNav from '../components/navs/SideNav/SideNav';
import Main from './Main';
import Home from './Home'
import Search from './Search';
import Library from './Library';
import Playlist from './Playlist';

import '../styles/App.css';
import tempSongList from '../utils/tempSongList';
import * as ROUTE from '../utils/routes';

const store = configureStore();

const APlayerOptions = {
  music: [...tempSongList],
  listMaxHeight: "80vh",
  fixed: true,
  listFolded: true,
}

function App() {
  return (
    <Provider store={store}>
      <Router basename='/spotikai'>
        <div className="App">
          <div className='side-nav'>
            <SideNav />
          </div>
          <div className='app-content'>
            <div className='container-fluid' >
              <Route exact path={ROUTE.HOME} component={Home} />
              <Route exact path={ROUTE.SEARCH} component={Search} />
              <Route exact path={ROUTE.LIBRARY} component={Library} />
              <Route exact path={ROUTE.PLAYLIST} component={Playlist} />
              <Redirect from='/' to={ROUTE.HOME} />
            </div>
          </div>
          <ReactAplayer {...APlayerOptions} />
        </div>
      </Router>
    </Provider>
  );
}

export default App;
